import React from 'react'
import { Card, Button, Icon,Radio} from 'antd'
// import ButtonGroup from 'antd/lib/button/button-group';
import './index.less'

export default class Buttons extends React.Component {
    state = {
        buttonsize: 'default',
        loading:true
    }
    // 1.绑定事件
    handlerChange = e => {
        //console.log('radio checked', e.target.value);
        this.setState({
          buttonsize: e.target.value,
        });
      };
    // 2.绑定this之后事件
    handleCloseLoading(){
        this.setState({
            loading: !this.state.loading
          });
        
    }

    render() {
        return (
            <div>
                <Card title="基础按钮" className='card_type'>
                    <Button type="primary">Primary</Button>
                    <Button>Default</Button>
                    <Button type="dashed">Dashed</Button>
                    <Button type="danger">Danger</Button>
                    <Button disabled>disabled</Button>
                </Card>
                <br />
                <Card title="图标按钮" className='card_type'>
                    <Button icon='plus'>创建</Button>
                    <Button icon='edit'>编辑</Button>
                    <Button type='danger' icon='delete'>删除</Button>
                    <Button shape='circle' icon='search'></Button>
                    <Button type='primary' icon='search'>搜索</Button>
                    <Button type='primary' icon='download'>下载</Button>
                </Card>
                <br />
                <Card title="loading按钮" className='card_type'>
                    <Button type='primary' loading={this.state.loading} icon='plus'>确定</Button>
                    <Button type='primary' shape='circle' icon='search' loading={this.state.loading} ></Button>
                    <Button loading={this.state.loading} icon='plus'>点击加载</Button>
                    <Button shape='circle' icon='search' loading={this.state.loading} ></Button>
                    <Button type='primary' onClick={this.handleCloseLoading.bind(this)}>关闭</Button>
                </Card>
                <br />
                <Card title="按钮组" bordered={false}>
                    <Button.Group>
                        <Button type="primary"><Icon type="left" />返回</Button>
                        <Button type="primary">继续<Icon type="right" /></Button>
                    </Button.Group>
                </Card>
                <br />
                <Card title="按钮尺寸" className='card_type'>
                    <Radio.Group onChange={this.handlerChange} value={this.state.buttonsize}>
                        <Radio value='large'>大</Radio>
                        <Radio value='default'>中</Radio>
                        <Radio value='small'>小</Radio>
                    </Radio.Group>
                    
                    <Button size={this.state.buttonsize} type='primary'>Primary</Button>
                    <Button size={this.state.buttonsize}>Default</Button>
                    <Button size={this.state.buttonsize} type="dashed">Dashed</Button>
                    <Button size={this.state.buttonsize} type="danger">Danger</Button>
                    
                </Card>
            </div>
        );
    }
}