import React from 'react'
import { Card, Col, Row, Modal } from 'antd'


export default class Gallery extends React.Component {
    state = {
        visible: false,
        currentImg: ''
    }

    handlerClick = (imgSrc) => {
        this.setState({
            visible: true,
            currentImg: imgSrc
        })

    }


    render() {
        const imgLists = []
        for (let i = 0; i < 4; i++) {
            const temp = []
            for (let j = 0; j < 6; j++) {
                const imgSrc = '/gallery/' + (4 * i + j + 1) + '.png'

                temp.push(<Card className='gallery_card' onClick={()=>this.handlerClick(imgSrc)} hoverable cover={
                    <img src={imgSrc} />
                }
                >
                    <Card.Meta
                        title="Card title"
                        description="This is the description"
                    />
                </Card>)
            }

            imgLists[i] = <Col span={6}>{temp} </Col>
        }
        console.log(imgLists);
        return (
            <div>
                <Row gutter={[{ xs: 8, sm: 16, md: 24, lg: 32 }, 20]}>
                    {imgLists}
                </Row>
                <Modal
                    title="图片画廊"
                    width={300}
                    visible={this.state.visible}
                    onCancel={()=>{ this.setState({
                        visible: false
                    })}}
                    footer={null}
                >
                    <img style={{width:'100%'}} src={this.state.currentImg} alt="" />
                </Modal>
            </div>




        )
    }
}