import React from 'react'
import { Carousel, Card } from 'antd'
import './index.less'

export default class Carousels extends React.Component {
    render() {
        return (
            <div>
                <Card title='文字轮播' bordered={false}>
                    <Carousel autoplay>
                        <div><h3>1</h3></div>
                        <div> <h3>2</h3></div>
                        <div><h3>3</h3></div>
                    </Carousel>
                </Card>
                <br/>
                <Card title='图片轮播' className='slide-carousel' bordered={false}>
                    <Carousel autoplay>
                        <div><img src='/carousel-img/carousel-1.jpg'/></div>
                        <div><img src='/carousel-img/carousel-2.jpg'/></div>
                        <div><img src='/carousel-img/carousel-3.jpg'/></div>
                    </Carousel>
                </Card>
            </div>
        )
    }
}