import React from 'react'
import { Card } from 'antd'
import G6 from '@antv/g6';
import dagre from 'dagre'



var data = {
    nodes: [{
        id: '收集日志'
    }, {
        id: '入 es 集群'
    }, {
        id: '入 hdfs'
    }, {
        id: 'hive 计算'
    }, {
        id: 'report'
    }],
    edges: [{
        source: '收集日志',
        target: '入 es 集群'
    }, {
        source: '收集日志',
        target: '入 hdfs'
    }, {
        source: '入 hdfs',
        target: 'hive 计算'
    }, {
        source: '入 es 集群',
        target: 'hive 计算'
    }, {
        source: 'hive 计算',
        target: 'report'
    }]
};

export default class Flow extends React.Component {

    componentDidMount() {
        this.init()
        
    }


    init = () => {
       
        var g = new dagre.graphlib.Graph();
        g.setDefaultEdgeLabel(function () {
            return {};
        });
        g.setGraph({
            rankdir: 'TB'
        });
        data.nodes.forEach(function (node) {
            node.label = node.id;
            g.setNode(node.id, {
                width: 150,
                height: 50
            });
        });
        data.edges.forEach(function (edge) {
            g.setEdge(edge.source, edge.target);
        });
        dagre.layout(g);
        var coord = void 0;
        g.nodes().forEach(function (node, i) {
            coord = g.node(node);
            data.nodes[i].x = coord.x;
            data.nodes[i].y = coord.y;
        });
        g.edges().forEach(function (edge, i) {
            coord = g.edge(edge);
            data.edges[i].startPoint = coord.points[0];
            data.edges[i].endPoint = coord.points[coord.points.length - 1];
            data.edges[i].controlPoints = coord.points.slice(1, coord.points.length - 1);
        });
        G6.registerNode('operation', {
            drawShape: function drawShape(cfg, group) {
                var rect = group.addShape('rect', {
                    attrs: {
                        x: -75,
                        y: -25,
                        width: 150,//label的宽
                        height: 50,
                        radius: 10,//弧度
                        stroke: '#00C0A5',//babel线的颜色
                        fill: '#92949F',//填充色
                        fillOpacity: 0.45,//填充透明度
                        lineWidth: 2//label线框
                    }
                });
                return rect;
            }
        }, 'single-shape');

        var graph = new G6.Graph({
            container: 'mountNode',
            width: window.innerWidth-400,
            height: window.innerHeight-100,
            pixelRatio: 2,

            defaultNode: {
                shape: 'operation',
                labelCfg: {
                    style: {
                        fill: '#666',
                        fontSize: 14,
                        fontWeight: 'bold'
                    }
                }
            },


        });
        graph.data(data);
        graph.render();
        graph.fitView();

    }

   




    render() {//div还没有开始渲染
        
        return (
            <div>
                <Card title="流程图">
                    <div id="mountNode"></div>
                </Card>

            </div>
        );
    }
}