import React from 'react';
import { HashRouter, Route, Redirect, Switch } from 'react-router-dom'
import App from './App';
import Admin from './admin';
import Login from './login';
import NoMatch from './noMatch';
import Home from './pages/home';
import Gallery from './pages/UI/gallery';
import Buttons from './pages/UI/button';
import Modals from './pages/UI/modals';
import Carousels from './pages/UI/carousel';
import FormLogin from './pages/form/login'
import Rich from './pages/rich';
import Loadings from './pages/UI/loadings';
import Messages from './pages/UI/messages';
import Notices from './pages/UI/notices'
import Tabs from './pages/UI/tabs'
import basciTable from './pages/table/basciTable';
import Bar from './pages/echarts&antv/bar';
import Pie from './pages/echarts&antv/pie';
import Line from './pages/echarts&antv/line';
import Flow from './pages/echarts&antv/flow';
import BOM from './pages/echarts&antv/part&bom';




export default class IRouter extends React.Component {
    render() {
        return (
            <HashRouter>
                <App>
                    <Switch>

                        <Route path='/login' component={Login} />
                        <Route path='/' render={() => {
                            return <Admin>
                                <Switch>
                                    <Route path="/admin/home" component={Home} />
                                    <Route path="/admin/ui/buttons" component={Buttons} />
                                    <Route path='/admin/ui/gallery' component={Gallery}></Route>
                                    <Route path='/admin/ui/modals' component={Modals}></Route>
                                    <Route path='/admin/ui/carousel' component={Carousels}></Route>
                                    <Route path='/admin/form/login' component={FormLogin}></Route>
                                    <Route path='/admin/ui/loadings' component={Loadings}></Route>
                                    <Route path='/admin/ui/messages' component={Messages}></Route>
                                    <Route path='/admin/ui/notification' component={Notices}></Route>
                                    <Route path='/admin/ui/tabs' component={Tabs}></Route>
                                    {/* 富文本编辑 */}
                                    <Route path='/admin/rich' component={Rich}></Route>
                                    {/* 表格 */}
                                    <Route path='/admin/table/basic' component={basciTable}></Route>
                                    <Route path='/admin/table/high' component={basciTable}></Route>
                                    {/* echarts */}
                                    <Route path='/admin/charts/bar' component={Bar}></Route>
                                    <Route path='/admin/charts/pie' component={Pie}></Route>
                                    <Route path='/admin/charts/line' component={Line}></Route>
                                    <Route path='/admin/charts/flow' component={Flow}></Route>
                                    <Route path='/admin/charts/bom' component={BOM}></Route>

                                    <Redirect to="/admin/home" />
                                    <Route component={NoMatch} />
                                </Switch>
                            </Admin>

                        }} />

                    </Switch>
                </App>
            </HashRouter>
        )
    }
}