import React from 'react'
import { Menu, Icon } from 'antd'
import MenuList from './../../config/menu'

import './index.less'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { switchMenu } from '../../redux/action'

//import 必须在其它所有业务代码前面（eslint 暴出），
// 异步组件导入了 也不能插在其他导入的东西前面，

const { SubMenu } = Menu
const logoUrl = 'https://gw.alipayobjects.com/zos/rmsportal/KDpgvguMpGfqaHPjicRK.svg'



class Navleft extends React.Component {
    state = {
        currentKey: ''
    }
    handleClick = ({ item, key }) => {
        const { dispatch } = this.props;
        
        dispatch(switchMenu(item.props.title))
        this.setState({
            currentKey: key 
        })

    }

    componentWillMount() {
        const menuTreeNode = this.renderMenu(MenuList)
        const currentKey = window.location.hash.replace(/#|\?.*$/g, '');
        
        // console.log(MenuList)
        // 1.setState之后发生了什么？
        // 2.是异步？第二个参数是回调可以获取一个参数里面的数据
        this.setState(() => ({ //render和强制刷新的时候会执行
            menuTreeNode,
            currentKey

        }), () => {
            // console.log(this.state);
        })
    }
    // 菜单渲染
    renderMenu = (data) => {
        return data.map((item) => {
            if (item.children) {
                return (
                    <SubMenu key={item.key} title={item.title}>
                        {this.renderMenu(item.children)}
                    </SubMenu>
                )

            }
            return <Menu.Item key={item.key} title={item.title}>
                <Link to={item.key}>{item.title}</Link>
            </Menu.Item>
        })

    }


    render() {
        return (
            <div className='navleft-wrap'>
                <div className='logo'>
                    <img src={logoUrl}></img>
                    <h1>V-MS</h1>
                </div>
                <Menu
                    onClick={this.handleClick}
                    selectedKeys={this.state.currentKey}
                    theme='dark'>
                    {this.state.menuTreeNode}
                </Menu>
            </div>

        );
    }
}
// 构建容器组件
export default connect()(Navleft);